// ============================================================================
// API ROUTE WRAPPER - ERROR HANDLING & VALIDATION
// ============================================================================
// Provides higher-order functions for standardizing API route behavior
// with proper error handling, validation, and response formatting.

import { NextRequest, NextResponse } from 'next/server';
import { z, ZodError } from 'zod';
import { checkRateLimit, RATE_LIMIT_PRESETS } from './rateLimit';
import { logger } from './logger';

// ============================================================================
// ERROR TYPES
// ============================================================================

export class ApiError extends Error {
  constructor(
    message: string,
    public statusCode: number = 500,
    public code: string = 'INTERNAL_ERROR',
    public details?: Record<string, unknown>,
    public retryAfter?: number
  ) {
    super(message);
    this.name = 'ApiError';
  }
}

export class ValidationError extends ApiError {
  constructor(message: string, public fieldErrors?: Record<string, string[]>) {
    super(message, 400, 'VALIDATION_ERROR', { fieldErrors });
  }
}

export class AuthenticationError extends ApiError {
  constructor(message: string = 'Authentication required') {
    super(message, 401, 'UNAUTHORIZED');
  }
}

export class AuthorizationError extends ApiError {
  constructor(message: string = 'Permission denied') {
    super(message, 403, 'FORBIDDEN');
  }
}

export class NotFoundError extends ApiError {
  constructor(resource: string = 'Resource') {
    super(`${resource} not found`, 404, 'NOT_FOUND');
  }
}

export class RateLimitError extends ApiError {
  constructor(message: string, retryAfter?: number) {
    super(message, 429, 'RATE_LIMIT_EXCEEDED', { retryAfter }, retryAfter);
  }
}

// ============================================================================
// ERROR HANDLER
// ============================================================================

export function handleApiError(error: unknown, context?: string): NextResponse {
  // Log the error
  logger.apiError(context || 'API', 'UNKNOWN', error, 500);

  // Handle Zod validation errors
  if (error instanceof ZodError) {
    const fieldErrors: Record<string, string[]> = {};
    const zodError = error as unknown as ZodError<unknown>;
    zodError.issues.forEach((e) => {
      const path = e.path.join('.');
      if (!fieldErrors[path]) {
        fieldErrors[path] = [];
      }
      fieldErrors[path].push(e.message);
    });

    return NextResponse.json(
      {
        success: false,
        error: 'Validation failed',
        code: 'VALIDATION_ERROR',
        details: { fieldErrors },
      },
      { status: 400 }
    );
  }

  // Handle custom API errors
  if (error instanceof ApiError) {
    const response: Record<string, unknown> = {
      success: false,
      error: error.message,
      code: error.code,
    };

    if (error.details) {
      response.details = error.details;
    }

    if (error.retryAfter) {
      response.retryAfter = error.retryAfter;
    }

    return NextResponse.json(response, { status: error.statusCode });
  }

  // Handle Firebase errors
  if (error instanceof Error) {
    const errorCode = (error as { code?: string }).code;

    if (errorCode?.startsWith('auth/')) {
      return NextResponse.json(
        {
          success: false,
          error: 'Authentication failed',
          code: 'AUTH_ERROR',
        },
        { status: 401 }
      );
    }

    if (errorCode === 'permission-denied') {
      return NextResponse.json(
        {
          success: false,
          error: 'You do not have permission to perform this action',
          code: 'PERMISSION_DENIED',
        },
        { status: 403 }
      );
    }
  }

  // Handle unknown errors
  console.error('Unhandled API error:', error);
  return NextResponse.json(
    {
      success: false,
      error: 'An unexpected error occurred',
      code: 'INTERNAL_ERROR',
    },
    { status: 500 }
  );
}

// ============================================================================
// API ROUTE OPTIONS
// ============================================================================

interface RouteOptions {
  // Authentication
  requireAuth?: boolean;

  // Rate limiting
  rateLimit?: keyof typeof RATE_LIMIT_PRESETS;

  // Validation
  validate?: {
    body?: z.ZodSchema;
    query?: z.ZodSchema;
    params?: z.ZodSchema;
  };

  // Caching
  cache?: {
    maxAge?: number;
    swr?: number;
  };

  // Logging
  logRequest?: boolean;
}

// ============================================================================
// API ROUTE WRAPPER
// ============================================================================

export function createApiRoute<T = unknown>(
  handler: (request: NextRequest, context: { params?: Record<string, string> }) => Promise<T>,
  options: RouteOptions = {}
) {
  return async function (request: NextRequest, context: { params?: Record<string, string> }) {
    const startTime = Date.now();

    try {
      // 1. Check rate limiting
      if (options.rateLimit) {
        const rateLimitResult = checkRateLimit(request, RATE_LIMIT_PRESETS[options.rateLimit]);

        if (!rateLimitResult.allowed) {
          throw new RateLimitError(
            RATE_LIMIT_PRESETS[options.rateLimit].message || 'Rate limit exceeded',
            Math.ceil((rateLimitResult.resetTime - Date.now()) / 1000)
          );
        }
      }

      // 2. Validate request body
      if (options.validate?.body) {
        try {
          const body = await request.json();
          options.validate.body.parse(body);
        } catch (error) {
          if (error instanceof ZodError) {
            throw new ValidationError('Invalid request body', error.flatten().fieldErrors);
          }
          throw new ValidationError('Invalid JSON in request body');
        }
      }

      // 3. Validate query parameters
      if (options.validate?.query && request.nextUrl.search) {
        const searchParams = Object.fromEntries(request.nextUrl.searchParams.entries());
        try {
          options.validate.query.parse(searchParams);
        } catch (error) {
          if (error instanceof ZodError) {
            throw new ValidationError('Invalid query parameters', error.flatten().fieldErrors);
          }
        }
      }

      // 4. Execute handler
      const result = await handler(request, context);

      // 5. Log request
      if (options.logRequest) {
        const duration = Date.now() - startTime;
        logger.info('API request completed', {
          metadata: {
            method: request.method,
            url: request.url,
            status: 200,
            duration,
          }
        });
      }

      // 6. Return response with caching headers
      const response = NextResponse.json({ success: true, data: result });

      if (options.cache) {
        if (options.cache.swr) {
          response.headers.set(
            'Cache-Control',
            `public, s-maxage=${options.cache.maxAge || 60}, stale-while-revalidate=${options.cache.swr}`
          );
        } else if (options.cache.maxAge) {
          response.headers.set(
            'Cache-Control',
            `public, max-age=${options.cache.maxAge}, immutable`
          );
        }
      }

      return response;
    } catch (error) {
      return handleApiError(error, `${request.method} ${request.url}`);
    }
  };
}

// ============================================================================
// RATE LIMIT WRAPPER
// ============================================================================

type RouteHandler = (request: NextRequest) => Promise<NextResponse>;

interface RateLimitConfig {
  maxRequests: number;
  windowMs: number;
  identifier?: string;
  message?: string;
}

type RateLimitPreset = keyof typeof RATE_LIMIT_PRESETS;

export function withRateLimit(
  handler: RouteHandler,
  config: RateLimitPreset | Partial<RateLimitConfig> = 'default'
): RouteHandler {
  const rateLimitConfig = typeof config === 'string' 
    ? RATE_LIMIT_PRESETS[config]
    : { 
        windowMs: config.windowMs || 60000, 
        maxRequests: config.maxRequests || 100, 
        message: config.message,
        keyPrefix: config.identifier ? `rl_${config.identifier}_` : 'rl_'
      };

  return async (request: NextRequest): Promise<NextResponse> => {
    const rateLimitResult = checkRateLimit(request, rateLimitConfig);

    if (!rateLimitResult.allowed) {
      return NextResponse.json(
        {
          success: false,
          error: rateLimitConfig.message || 'Rate limit exceeded',
          code: 'RATE_LIMIT_EXCEEDED',
          retryAfter: Math.ceil((rateLimitResult.resetTime - Date.now()) / 1000),
        },
        {
          status: 429,
          headers: {
            'Retry-After': String(Math.ceil((rateLimitResult.resetTime - Date.now()) / 1000)),
          },
        }
      );
    }

    return handler(request);
  };
}

// ============================================================================
// ASYNC WRAPPER FOR SAFETY
// ============================================================================

interface ErrorHandlingOptions {
  endpoint?: string;
  method?: string;
  skipRateLimit?: boolean;
}

export function withErrorHandling<T extends RouteHandler>(
  fn: T, 
  _options?: ErrorHandlingOptions
): T {
  return (async (request: NextRequest): Promise<NextResponse> => {
    try {
      return await fn(request);
    } catch (error) {
      console.error('Function error:', error);
      throw error;
    }
  }) as T;
}

// ============================================================================
// RESPONSE HELPERS
// ============================================================================

export function successResponse<T>(data: T, status = 200): NextResponse {
  return NextResponse.json(
    { success: true, data },
    { status }
  );
}

export function errorResponse(
  message: string,
  code: string = 'ERROR',
  status: number = 400,
  details?: Record<string, unknown>
): NextResponse {
  return NextResponse.json(
    { success: false, error: message, code, details },
    { status }
  );
}

export function created<T>(data: T): NextResponse {
  return successResponse(data, 201);
}

export function noContent(): NextResponse {
  return new NextResponse(null, { status: 204 });
}

// ============================================================================
// PAGINATION HELPERS
// ============================================================================

interface PaginationParams {
  page?: number;
  limit?: number;
  orderBy?: string;
  order?: 'asc' | 'desc';
}

interface PaginationResult<T> {
  data: T[];
  pagination: {
    page: number;
    limit: number;
    total: number;
    totalPages: number;
    hasMore: boolean;
  };
}

export function parsePagination(request: NextRequest): PaginationParams {
  const searchParams = request.nextUrl.searchParams;

  return {
    page: Math.max(1, parseInt(searchParams.get('page') || '1', 10)),
    limit: Math.min(100, Math.max(1, parseInt(searchParams.get('limit') || '20', 10))),
    orderBy: searchParams.get('orderBy') || 'createdAt',
    order: (searchParams.get('order') as 'asc' | 'desc') || 'desc',
  };
}

export function paginateResponse<T>(
  data: T[],
  params: PaginationParams,
  total: number
): PaginationResult<T> {
  const { page = 1, limit = 20 } = params;
  const totalPages = Math.ceil(total / limit);
  const start = (page - 1) * limit;
  const end = start + limit;
  const paginatedData = data.slice(start, end);

  return {
    data: paginatedData,
    pagination: {
      page,
      limit,
      total,
      totalPages,
      hasMore: page < totalPages,
    },
  };
}

